.contacts-form {
  border: 1px solid black;
  width: 350px;
  padding: 20px;
  border-radius: 10px;
  background-color: yellowgreen;
  margin-bottom: 30px;

  box-shadow: 10px 10px 8px 2px rgba(0, 0, 0, 0.6);
}
.contacts-form__name-label {
  display: flex;
  flex-direction: column;
  margin-bottom: 10px;
  padding-right: 50px;
  padding-top: 10px;
}
.contact-form__input {
  display: block;
}
.new-contact-btn {
  cursor: pointer;
  padding: 8px 16px;
  border-radius: 4px;
  color: white;
  background-color: blue;
  font-weight: 500;
  height: 35px;
  border: none;
  padding: 8px 16px;
  border: 2px solid purple;
}
